body{margin:0px;padding:0px;overflow-x:hidden}*{font-family:"Montserrat", sans-serif}.lit-spinner,.lit-spinner-black,.lit-spinner-big{width:30px;height:30px;border:2.5px solid transparent;border-top:2.5px solid #f3f3f3;border-bottom:2.5px solid #f3f3f3;border-radius:50%;animation:spin 1s linear infinite}.lit-spinner-black,.lit-spinner-big{border-top:2.5px solid #000;border-bottom:2.5px solid #000}.lit-spinner-big{width:6rem !important;height:6rem !important}@keyframes spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}::-webkit-scrollbar{height:8px;width:10px}::-webkit-scrollbar-track{background:#f1f1f1}::-webkit-scrollbar-thumb{background:linear-gradient(to bottom, #ff1b1b, #ff6f4b);border-radius:5px;-webkit-transition:0.3s;transition:0.3s}::-webkit-scrollbar-thumb:hover{background:linear-gradient(to bottom, rgba(255,27,27,0.768627), rgba(255,78,34,0.733333))}::-webkit-scrollbar-thumb:active{background:linear-gradient(to bottom, rgba(255,27,27,0.690196), rgba(255,111,75,0.741176))}body.dark-mode{background-color:#161616 !important;color:#fff !important}body.dark-mode .contacts-label{border-bottom:solid 1px #353535 !important}body.dark-mode .row.contact{border-bottom:solid 1px #353535 !important}body.dark-mode .row.contact:hover,body.dark-mode .row.contact:focus{cursor:pointer;background:#ff6958 !important}body.dark-mode #chat-box{background-color:black}body.dark-mode .contact-chat .chat-body{background-color:#383838 !important}body.dark-mode .contact-chat .chat-body::before{background:#383838 !important}body.dark-mode form#input-box input{background-color:#252525;color:white}body.dark-mode .modal{color:black}body.dark-mode form.sl-form{color:black}body.dark-mode .chat-body::after{background:black !important}body.dark-mode #landing-features{background-color:white !important}body.dark-mode ::-webkit-scrollbar-track{background:#161616 !important}

