e:hover,.contact-request .btn-resolve.decline:focus{background-color:#ce2722}.contact-request .btn-resolve.decline:active{background-color:#c52822}img.profile-img{border-radius:50%;object-fit:cover}#input-box{width:100%;position:fixed;bottom:0px}#input-box input{padding:20px;outline:none;border:none;box-shadow:0 0 5px rgba(51,51,51,0.1);width:calc(75% - 100px)}#input-box #input-submit{width:100px;height:65px;border:none;position:absolute;bottom:0px !important;background-image:linear-gradient(135deg, #ff3535, #ff613a) !important;color:white;font-size:20px;transition:200ms;border-radius:0%;outline:none}#input-box #input-submit:hover{opacity:0.9}#input-box #input-submit:active{opacity:0.8}@media screen and (max-width: 992px){#input-box input{width:calc(100% - 100px)}.navbar-btn-outline{margin:10px 0px !important}#chat-box{height:calc(100vh - 200px) !important}#convo-header{text-align:center}}.chat{width:100%;display:flex;flex-direction:column;padding:5px 10px}.chat .chat-body{border-radius:5px;padding:7px 13px;display:inline-block;max-width:100%;word-wrap:break-word}.chat .chat-template,.chat .contact-chat,.chat .user-chat{display:flex;flex-direction:column}.chat .chat-body-before,.chat .contact-chat .chat-body:before,.chat .user-chat .chat-body:before{content:"";position:absolute;z-index:0;bottom:0;height:10px;width:17px}.chat .chat-body-after,.chat .contact-chat .chat-body:after,.chat .user-chat .chat-body:after{content:"";position:absolute;z-index:1;background:white;bottom:0;width:10px;height:20px}.chat .contact-chat{align-items:flex-start}.chat .contact-chat .chat-body{background-color:#eee;position:relative}.chat .contact-chat .chat-body:before{left:-5px;background:#eee;border-bottom-right-radius:15px}.chat .contact-chat .chat-body:after{left:-10px;border-bottom-right-radius:10px}.chat .user-chat{align-items:flex-end}.chat .user-chat .chat-body{color:white;background:linear-gradient(to top, #ff0c0c, #ff6b08);background-attachment:fixed;position:relative}.chat .use